.backdrop {
  @include transition(opacity 300ms linear);
  background-color: rgba(0, 0, 0, .2);
  height: 100%;
  left: 0;
  opacity: 0;
  position: fixed;
  top: 0;
  visibility: hidden;
  width: 100%;
  z-index: map-get($zindex, backdrop);

  &.active {
    opacity: 1;
    visibility: visible;
  }
}
